import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/components/common/api';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import * as _ from 'lodash';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { FilterCustomBean } from "app/pages/tzb/custom/custom-information/advanced-search/search-template-manage/bean/filterCustom.bean";

@Component({
  selector: 'app-search-template-manage',
  templateUrl: './search-template-manage.component.html',
  styleUrls: ['./search-template-manage.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class SearchTemplateManageComponent implements OnInit {
  totalNum;
   //
   public ch: any = CALENDAR_CN;
   public creatStartTime: any;//
   public creatEndTime: any;//
   //
   public tableData: any = [];
   //bean
   public filterCustomBean: FilterCustomBean = new FilterCustomBean();
   public total: number;//
   //
   public msgs: Message[] = [];
   //
   public shareRangeOpt: any[];//
   public TempStatusOpt: any[] //
   public createOrgId: string = '';//
   public creatOrgOpt: any = [{ orgName: "", orgId: "" }];//
   public creatOrgOpts: any = [];//
   public creatPeopleOpts: any = [];//
   public orgId: string = '';//
   public orgName: string = '';//
   public location: any = [];//
   public showOrgDis: boolean = false;//
   public PublishTempShareRanOpt: any[];//
   //
    code:any;
   constructor(
     public router: Router,
     public httpService: CusOperationHttpService,
     public commfunc: Commfunc
   ) {
     this.codeValues();
     this.shareRangeOpt=this.code['ShareRangeOpt'];
     this.TempStatusOpt=this.code['TempStatusOpt'];
     this.PublishTempShareRanOpt=this.code['PublishTempShareRanOpt']
     this.tableData = [
       {screenTemplateId:'1'},
       {screenTemplateId:'2'},
     ]
   }
 //
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code
  }
   ngOnInit() {
     this.filterCustomBean.pageSize = 10;
     this.filterCustomBean.pageNum = 1;
     /**
      * id
      * this.filterCustomBean.createOrgId
      */
     //session
     this.orgId = (JSON.parse(this.commfunc.getSessionData('commonHeader'))).orgId;
     let param = { orgId: this.orgId, relationshipType: "0" };//relationshipType="0"
     this.httpService.queryOrgByOrgIdAndRelationship(param).subscribe(data => {
       
       if (data.returnCode.code == "success") {
         if (data.orgList[0].location) {
           this.location = data.orgList[0].location.split(',');//
           /**
            * creatOrgOpt
            */
           this.location.forEach(item => {
             let param = { orgId: item, relationshipType: "0" };
             this.searchTree(param);
           });
           //,
           this.filterCustomBean.createOrgId = this.location;
           this.query();
         }
 
         this.msgs = [];
         this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
       } else {
         this.msgs = [];
         this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
       }
     }, error => {
       this.msgs = [];
       this.msgs.push({ severity: 'error', summary: '', detail: '' });
     })
 
   }
 
   //7
   shareRangeClick() {
     if (this.filterCustomBean.sharedScope == '7') {
       this.createOrgId = '';//
       this.filterCustomBean.createUserId = '';//
       this.showOrgDis = true
     } else {
       this.showOrgDis = false;
     }
   }
   //
   creatOrgIdClick() {
     //
     let param = { operatingOrgId: this.createOrgId, pageNum: "1", pageSize: "999999" }
     this.httpService.queryTellerByCombConditions(param).subscribe(data => {
       if (data.returnCode.code == "success") {
         this.creatPeopleOpts = _.sortBy(this.commfunc.toChangeTree(data.tellerList, "tellerName", "tellerId"), 'value');
         this.creatPeopleOpts.unshift({ label: "", value: '' });
         this.msgs = [];
         this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
       } else {
         this.msgs = [];
         this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
       }
     }, error => {
       this.msgs = [];
       this.msgs.push({ severity: 'error', summary: '', detail: '' });
     })
 
   }
 
   //
   searchTree(param) {
     this.httpService.queryOrgByOrgIdAndRelationship(param).subscribe(data => {
       if (data.returnCode.code == "success") {
         this.creatOrgOpt.push(data.orgList[0]);
         this.creatOrgOpts = _.sortBy(this.commfunc.toChangeTree(this.creatOrgOpt, "orgName", "orgId"), 'value');
         this.msgs = [];
         this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
       } else {
         this.msgs = [];
         this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
       }
     }, error => {
       this.msgs = [];
       this.msgs.push({ severity: 'error', summary: '', detail: '' });
     })
   }
 
 
   // id
   queryClick() {
     this.filterCustomBean.marketingTimeFrom = this.commfunc.transDateN(this.creatStartTime);
     this.filterCustomBean.marketingTimeTo = this.commfunc.transDateN(this.creatEndTime);
     this.filterCustomBean.createOrgId = this.createOrgId.split(',');//
     this.query();
   }
 
   //
   query() {
     this.httpService.queryMyCustAdvanceSeachTempList(this.filterCustomBean).subscribe(data => {
       if (data.returnCode.code == 'success') {
         this.tableData = data.screenTemplateList;
         this.total = data.tatalCount;
         this.msgs = [];
         this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
       } else {
         this.msgs = [];
         this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
       }
     }, error => {
       this.msgs = [];
       this.msgs.push({ severity: 'error', summary: '', detail: '' });
     })
   }
 
   // ID
   showDetail(item) {
     this.router.navigate(['/pages/tzb/custom/custom-information/advanced-search/template-detail', { screenTemplateId: item.screenTemplateId }]);
   }
 
   //
   paginate(event) {
     //
     this.filterCustomBean.pageSize = event.rows * 1;
     //
     let currentPage = event.page + 1;
     this.filterCustomBean.pageNum = currentPage;
     //
     this.query();
     this.filterCustomBean.pageNum = 1;
   }
 
   //
   resetClick() {
     this.filterCustomBean = new FilterCustomBean();
     this.creatStartTime = null;
     this.creatEndTime = null;
     this.createOrgId='';
   }
}
