	productTable() {
		// 
		// if (this.filterTemplate) {
		this.headerTable = [];
		this.dataTable = [];
		this.dataTable1 = [];
		let headerSave: any = [], headerLoans: any = [], tabData: any = [];

		// displayTarget
		this.displayTarget.forEach((v, i) => {
			this.headerTable.push(
				{ field: v.fieldName, header: v.fieldTitle, colspan: "", rowspan: "2" },
			)
			if (v.fieldName == '') {
				this.headerTable[i].rowspan = '1';
				this.headerTable[i].colspan = v.subList2.length;
				if (v.subList2.length > 0) {
					v.subList2.forEach((v1) => {
						this.dataTable.push(
							{ field: v1.fieldName, header: v1.fieldTitle, colspan: "1" }
						)
					})
				}
			}
			if (v.subList.length > 0) {
				if (v.fieldTitle == '') {
					v.subList.forEach(v1 => {
						headerSave.push(v1.fieldTitle)
					})
				} else if (v.fieldTitle == '') {
					v.subList.forEach(v1 => {
						headerLoans.push(v1.fieldTitle)
					})
				}

			}
			if (v.field !== '' && v.subList2.length == 0 && v.fieldName !== "CUST_ID" && v.fieldName !== "CUST_NAME") {
				this.dataTable1.push(
					{ field: v.fieldName, header: v.fieldTitle, colspan: "", rowspan: "2" },
				)
			}
		})
		if (headerSave.length > 0) {
			this.headerTable[2].header = `${'(' + headerSave + ')'}`;
		} else {
			this.headerTable[2].header = `${''}`;
		}
		if (headerLoans.length > 0) {
			this.headerTable[3].header = `${'(' + headerLoans + ')'}`;
		} else {
			this.headerTable[3].header = `${''}`;
		}


	}

	// dom   
	getDomMethod() {
		let dom_gundam: any = document.getElementById('domGundam').getElementsByTagName('thead');
		this.displayTarget.map((v, i) => {
			if (v.subList2.length > 0) {
				// let dom_gundam1 = dom_gundam[0].getElementsByTagName('table');
				dom_gundam[0].children[0].children[i + 1].style.width = `${(v.subList2.length / 1) * 112 + 'px'}`;
			}
		})
	}
	onFilter() {//
		this.ftpAttrList = this.FtpTo;
		this.checkSave = [];
	}
	func() {
		this.FtpTo = JSON.parse(JSON.stringify(this.ftpAttrList));
		this.ftpAttrList.forEach(items => {
			items.forEach(item => {
				if (item.profitParmFrom && item.profitParmTo) {
					item['profitParmFrom'] = `${item['profitParmFrom']}|${item['profitParmTo']}` + '';
					delete item['profitParmTo'];
				} else {
					if (item.profitParmTo) {
						item['profitParmFrom'] = `*|${item['profitParmTo']}` + '';
						delete item['profitParmTo'];
					} else if (item.profitParmFrom) {
						item['profitParmFrom'] = `${item['profitParmFrom']}|*` + '';
					}
				}

			})
		})
	}


	//
	fielBackView(list) {
		for (let i = 0; i < list.length; i++) {
			for (let j = 0; j < this.custExplTable.length; j++) {
				if (list[i].flag == this.custExplTable[j].flag) {
					this.custExplTable.splice(j, 1, list[i]);
					break;
				}
			}
		}
	}
