	chooseSelect() {
		this.generalAttr = [];
		this.presonAttr = [];
		this.companyAttr = [];
		this.saveAttr = [];
		this.loansAttr = [];
		this.FTPAttr = [];
		this.baseAttr = [];
		this.acceptAttr = [];
		this.mattersAttr = [];
		this.creditAttr = [];
		this.pointAttr = [];
		this.onlineAttr = [];
		this.WX_Attr = [];
		this.phoneAttr = [];

		/*---------------------------- - -------------------------*/

		this.custExplTable.forEach(v => {

			// 
			if (v.property === 'generalAttr') {
				this.generalAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "generalAttr", value: v.value })
			} else if (v.property === 'presonAttr') {
				// 
				this.presonAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "presonAttr", value: v.value })
			} else if (v.property === 'companyAttr') {
				// 
				this.companyAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "companyAttr", value: v.value })
			} else if (v.property === 'saveAttr') {
				// 
				this.saveAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "saveAttr", value: v.value })
			} else if (v.property === 'loansAttr') {
				// 
				this.loansAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "loansAttr", value: v.value })
			} else if (v.property === 'FTPAttr') {
				// FTP
				this.FTPAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "FTPAttr", value: v.value })
			} else if (v.property === 'acceptAttr') {
				// 
				this.acceptAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "acceptAttr", value: v.value })
			} else if (v.property === 'mattersAttr') {
				// 
				this.mattersAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "mattersAttr", value: v.value })
			} else if (v.property === 'creditAttr') {
				// 
				this.creditAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "creditAttr", value: v.value })
			} else if (v.property === 'pointAttr') {
				// 
				this.pointAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "pointAttr", value: v.value })
			} else if (v.property === 'onlineAttr') {
				// 
				this.onlineAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "onlineAttr", value: v.value })
			} else if (v.property === 'WX_Attr') {
				// 
				this.WX_Attr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "WX_Attr", value: v.value })
			} else if (v.property === 'phoneAttr') {
				// 
				this.phoneAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "phoneAttr", value: v.value })
			} else {
				this.baseAttr.push({ label: v.label, isDisabled: v.isDisabled, isCheck: v.isCheck, property: "baseAttr", value: v.value })
			}

			this.filterTemplate.forEach((v1, i) => {
				if (v.label == v1.label) {
					this.filterTemplate[i].custName = v.value;
				}
			})
		})

		/*--------------------------------------------------------------------------*/
	}
	// 
	// 
	chooseItem(data) {
		let all = true;
		this.custExplTable.forEach(item => {
			if (data.label == item.label) {
				item.isCheck = !item.isCheck;
			}
		});
		if (data.isCheck) {
			this.PersonCheck.push(data);
			this.custExplTable.forEach(item => {
				all = item.isCheck && all
			});
		} else {
			this.PersonCheck.forEach((item, i) => {
				if (item.value == data.value) {
					this.PersonCheck.splice(i, 1);
					return;
				}
			})
			all = false;
		}
		this.PersonCheck.sort((a, b) => {
			return a.orderCode > b.orderCode
		})
		this.isAll = all;
		console.log(data)
	}
	// 
	checkAll2() {
		let isTrue = this.isAll;
		this.PersonCheck = [];
		this.custExplTable.forEach(item => {
			item['isCheck'] = isTrue;
			if (isTrue || item.isDisabled) {
				this.PersonCheck.push(item);
			}
		});
		this.PersonCheck.sort((a, b) => {
			return a.orderCode > b.orderCode
		})
		this.chooseSelect();
	}

