	// 
	download() {
		if (this.custExplTable1 && this.custExplTable1.length > 0) {
			this.creatCustExplCondBean.explortFlag = "1";
			this.creatCustExplCondBean.postId = this.postId;
			this.creatCustExplCondBean.displayTarget = this.disPlayT2;
			let this_id = this.extParam.postId;
			let flag = true;
			for (let item of this.new_postId) {
				if (this_id == item.value) {
					flag = false;
					break
				}
			}
			if (!flag) {
				this.downloadComplete();
			} else {
				let lcbl: any = {
					lctybl: this.extParam.postId
				}
				this.outList = this.commfunc.query('XZZXLC', lcbl);
				if (!this.outList.erro) {
					this.outVal = this.outList.waitSelUserList;
					let chooseThis = this.outList.display;
					let Header = JSON.parse(this.commfunc.getSessionData('commonHeader'));
					let a2 = Header.extParam.specifyNextOpers;
					if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
						this.downloadPerson = true;
					} else {
						this.downloadComplete();
					}
				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: this.outList.erro });
				}
			}
		} else {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: "" });
			return;
		}

		// this.msgs = [];
		// this.msgs.push({ severity: 'error', summary: '', detail: '' });
	}

