	//
	addCustLabelAndCustLabelType() {
		if (!this.customSignBean.labelName || this.customSignBean.labelName == '') {
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
			return;
		}
		if (!this.customSignBean.shareFlag || this.customSignBean.shareFlag == '') {
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
			return;
		}
		this.customSignBean.labelType = '2';
		this.customSignBean.startDate = this.commfunc.transDateN(new Date());
		// this.customSignBean.endDate = this.commfunc.transDate(this.customSignBean.endDate);
		this.customSignBean.creatorId = this.creatorId;
		this.customSignBean.creatorName = this.creatorName;
		this.customSignBean.status = '1';
		let param1 = _.clone(this.customSignBean);
		param1.endDate = this.commfunc.transDateN(param1.endDate);
		this.cusViewHttpService.addLabelInfo(param1).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.selectCustLabelType();
				let param = {
					labelType: this.customSignBean.labelType,
					labelName: this.customSignBean.labelName,
					labelDesc: this.customSignBean.labelDesc,
					startDate: this.customSignBean.startDate,
					endDate: this.commfunc.transDateN(this.customSignBean.endDate),
					labelNum: data.labelNum,
					creatorId: this.creatorId,
					creatorName: this.creatorName,
					shareFlag: this.customSignBean.shareFlag,
				}
				let labelArray = [];
				labelArray = this.labelData1;
				if (labelArray.length > 0) {
					for (let labelitem of this.labelData1) {
						if (labelitem.labelName == param.labelName) {
							this.msgs = [{ severity: 'error', summary: '', detail: '' }];
							return;
						}
					}
					labelArray.push(param);
					this.labelData1 = labelArray;
					this.labelData = this.labelData1;
				} else {
					labelArray.push(param);
					this.labelData1 = labelArray;
					this.labelData = this.labelData1;
				}
				this.customSignBean = new CustomSignBean();
				this.customSignBean.shareFlag = '0';

			} else {
				data.returnCode.message ? data.returnCode.message : '';
				this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
			}
		});
	}

	//
	subBtn() {
		debugger
		let requestMapCopy = JSON.parse(JSON.stringify(this.creatCustExplCondBean.requestMap));
		let requestMap = this.deleteNull(this.deleteNull(requestMapCopy));
		this.changeSelectName(requestMap.custGroupAttrList, 'custGrouName', 'cusGroupNameOpt');
		this.changeSelectName(requestMap.custLabelAttrList, 'labelName', 'cusTagNameOpt');
		//
		let custArr = [];
		if (this.chooseData) {
			this.chooseData.forEach(element => {
				custArr.push({
					CUST_ID: element.CUST_ID,
				})
			});
		}
		if (this.checkAll == true && this.num == 3) {//
			this.paramoObj = {
				labelList: this.labelData,
				custList: custArr,
				pageNum: this.pageNum,
				pageSize: this.pageSize,
				requestType: "1",
				statusId: "3",
				marketFlag: "0",
				expCondName: this.creatCustExplCondBean.expCondName,
				requestMap,
				userName: this.creatCustExplCondBean.userName,
				totalCount: this.totalNum,
				labelCheckAll: '1',
				expCondId: this.exploreConditionTempId,
				// shareFlag:this.shareFlag,
			}

		} else if (this.flagCheckAll == '1' || this.num == 2) {//
			this.paramoObj = {
				labelList: this.labelData,
				custList: [],
				pageNum: this.pageNum,
				pageSize: this.pageSize,
				requestType: "1",
				statusId: "3",
				marketFlag: "0",
				expCondName: this.creatCustExplCondBean.expCondName,
				requestMap,
				userName: this.creatCustExplCondBean.userName,
				totalCount: this.totalNum,
				labelCheckAll: '1',
				expCondId: this.exploreConditionTempId,
				// shareFlag:this.shareFlag,
			}
		} else if (this.num == 3 && this.checkAll == false) {//
			this.paramoObj = {
				labelList: this.labelData,
				custList: custArr,
				pageNum: this.pageNum,
				pageSize: this.pageSize,
				requestType: "1",
				statusId: "3",
				marketFlag: "0",
				expCondName: this.creatCustExplCondBean.expCondName,
				requestMap,
				userName: this.creatCustExplCondBean.userName,
				totalCount: this.totalNum,
				labelCheckAll: '0',
				expCondId: this.exploreConditionTempId,
				// shareFlag:this.shareFlag,
			}
		}
		if (this.labelData.length > 0) {
			this.cusViewHttpService.addCustLabelList(this.paramoObj).subscribe(data => {
				if (data.returnCode.code == 'success') {
					this.customSignBean.shareFlag = '0';
					this.custagadd = false;
					this.tipClick(data.messageCode);
					if (this.flagCheckAll == '1' && this.num == 2) {
						this.saveAndLook(3);
						this.checkAllClick();
						this.checkAll = true;
					} else if (this.num == 3 && this.checkAll == true) {
						this.checkAllClick();
						this.checkAll = true;
					}

					this.msgs = [{ severity: 'success', summary: '', detail: data.returnCode.message }];
				} else {
					data.returnCode.message ? data.returnCode.message : '';
					this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
				}
			});
		} else {
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
		}
	}

